@font-face {
  font-family: Metdemo;
  src: url("./Metdemo.ttf"); }

body {
  margin: 0;
  background: black;
  color: white;
  user-select: none; }

[acc] {
  font-family: Metdemo; }

.App > .button {
  position: absolute;
  top: calc(5% - 20px);
  background-image: url("../assets/imgs/left.svg");
  background-size: 40px 40px;
  width: 40px;
  height: 40px; }
  .App > .button.left {
    left: 20px; }
  .App > .button.right {
    right: 20px;
    transform: scaleX(-1); }

.ModesView {
  width: 100vw;
  height: 100vh;
  overflow: hidden; }
  .ModesView .titles {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom: 4px solid white;
    height: 10%;
    font-size: 20px; }
  .ModesView .title.shown {
    font-weight: bold;
    text-decoration: underline; }
  .ModesView .lists {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    height: 90%;
    overflow: scroll; }
  .ModesView .ModeList > .name {
    font-size: 20px;
    margin: 20px 0 10px 0;
    border-bottom: 1px dotted white; }
  .ModesView .ModeList .Mode {
    margin: 10px 0; }

.BoardView {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 100vh; }

.EarTrainingView {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 100vh; }
  .EarTrainingView .message {
    font-size: 40px;
    width: 200px; }
    .EarTrainingView .message .yep,
    .EarTrainingView .message .nope {
      position: absolute;
      width: 200px;
      text-align: center;
      opacity: 0;
      transition: opacity .3s ease; }
      .EarTrainingView .message .yep.show,
      .EarTrainingView .message .nope.show {
        opacity: 1;
        transition: opacity 0s; }
    .EarTrainingView .message .yep {
      color: #53d053; }
    .EarTrainingView .message .nope {
      color: #d05353; }
  .EarTrainingView .controls {
    display: flex;
    justify-content: space-evenly;
    width: 100%; }
  .EarTrainingView .root,
  .EarTrainingView .type > .harm,
  .EarTrainingView .type > .melo,
  .EarTrainingView .speed > .eighth,
  .EarTrainingView .speed > .quarter,
  .EarTrainingView .speed > .half {
    width: 80px;
    height: 80px;
    border: 2px solid transparent;
    border-radius: 15px;
    vertical-align: top;
    transition: border-color .3s ease; }
    .EarTrainingView .root.selected,
    .EarTrainingView .type > .harm.selected,
    .EarTrainingView .type > .melo.selected,
    .EarTrainingView .speed > .eighth.selected,
    .EarTrainingView .speed > .quarter.selected,
    .EarTrainingView .speed > .half.selected {
      border-color: white; }
  .EarTrainingView .root {
    background-image: url("../assets/imgs/random.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 70px; }
  .EarTrainingView .type > div {
    display: inline-block; }
  .EarTrainingView .type .harm {
    background-image: url("../assets/imgs/harm.png");
    background-size: 50px 80px;
    background-repeat: no-repeat;
    background-position: center; }
  .EarTrainingView .type .melo:before, .EarTrainingView .type .melo:after {
    content: '';
    display: inline-block;
    width: 25px;
    height: 80px; }
  .EarTrainingView .type .melo:before {
    margin-left: 12.5px;
    background-image: url("../assets/imgs/melo1.png"); }
  .EarTrainingView .type .melo:after {
    background-image: url("../assets/imgs/melo2.png"); }
  .EarTrainingView .speed {
    font-size: 40px;
    transition: opacity .3s ease; }
    .EarTrainingView .speed.hide {
      opacity: .2;
      pointer-events: none; }
      .EarTrainingView .speed.hide > div {
        border-color: transparent !important; }
    .EarTrainingView .speed > div {
      display: inline-block;
      text-align: center; }
  .EarTrainingView .play {
    background-image: url("../assets/imgs/play.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 80px;
    width: 80px;
    height: 80px; }

.ModeList {
  text-align: center; }
  .ModeList > .name {
    font-weight: bold; }

.Mode {
  width: 416px;
  text-align: center; }
  .Mode .Box {
    width: 30px;
    height: 30px;
    line-height: 22px;
    margin: 1px;
    padding: 1px;
    border-radius: 7.5px;
    border-width: 3px;
    color: black;
    font-size: 15px;
    cursor: initial; }
    .Mode .Box.selected {
