.Guitar .inlay.single:before, .Guitar .inlay.double:before, .Guitar .inlay.double:after {
  content: '';
  position: absolute;
  top: calc(50% - 17.5px / 2);
  display: block;
  background: white;
  width: 17.5px;
  height: 17.5px;
  border-radius: 17.5px; }

.Guitar .String {
  display: flex; }
  .Guitar .String .Box {
    width: 70px;
    height: 70px;
    line-height: 49px;
    margin: 3.5px;
    padding: 3.5px;
    border-radius: 17.5px;
    border-width: 7px;
    color: black;
    font-size: 35px;
    cursor: initial; }
    .Guitar .String .Box.selected {
      border-color: white; }
    .Guitar .String .Box:first-child {
      width: 35px;
      font-size: 17.5px; }

.Guitar .inlay {
  width: 70px;
  height: 70px;
  line-height: 49px;
  margin: 3.5px;
  padding: 3.5px;
  border-radius: 17.5px;
  border-width: 7px;
  color: black;
  font-size: 35px;
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  height: 17.5px; }
  .Guitar .inlay.selected {
    border-color: white; }
  .Guitar .inlay:first-child {
    width: 35px; }
  .Guitar .inlay.single:before {
    left: calc(50% - 17.5px / 2); }
  .Guitar .inlay.double:before {
    left: calc((100% - 17.5px) / 4); }
  .Guitar .inlay.double:after {
    left: calc(3 * (100% - 17.5px) / 4); }

